import React, { useState, useEffect } from 'react';
import { Form, Breadcrumb, Tabs, Table, Input, Icon, Button, message } from 'antd';
import { injectIntl } from 'react-intl';
import Highlighter from 'react-highlight-words';

import { getServices } from '../../services';
const { TabPane } = Tabs;

const ServicesCmp = props => {
  const {
    intl: { messages },
  } = props;

  const [loading, setLoading] = useState(true);
  const [searchText, setSearchText] = useState('');
  const [searchInput, setSearchInput] = useState('');
  const [services, setServices] = useState([]);

  useEffect(() => {
    fetchServices();
  }, []);

  const fetchServices = async () => {
    try {
      const response = await getServices();
      const {
        data: { services },
      } = response;
      setServices(services);
      setLoading(false);
    } catch (error) {
      message.error(error);
    }
  };

  const getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            setSearchInput(node);
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => handleSearch(selectedKeys, confirm)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => handleSearch(selectedKeys, confirm)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput.select());
      }
    },
    render: text => (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[searchText]}
        autoEscape
        textToHighlight={text.toString()}
      />
    ),
  });

  const handleSearch = (selectedKeys, confirm) => {
    confirm();
    setSearchText(selectedKeys[0]);
  };

  const handleReset = clearFilters => {
    clearFilters();
    setSearchText('');
  };

  const columns = [
    {
      title: messages.models.name,
      dataIndex: 'name',
      key: 'name',
      render: (text, record) => <span>{text}</span>,
      ...getColumnSearchProps('name'),
      width: 180,
    },
    {
      title: messages.models.descr,
      dataIndex: 'descr',
      key: 'descr',
      ...getColumnSearchProps('descr'),
    },
  ];

  return (
    <div>
      <Breadcrumb style={{ margin: '16px 0' }}>
        <Breadcrumb.Item>{messages.menus.services}</Breadcrumb.Item>
      </Breadcrumb>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        <Tabs defaultActiveKey="all">
          <TabPane tab={messages.engines.allServices} key="all">
            <Table
              size="small"
              loading={loading}
              columns={columns}
              dataSource={services}
              rowKey={record => record.id}
            />
          </TabPane>
        </Tabs>
      </div>
    </div>
  );
};
const WrappedServicesCmp = Form.create()(injectIntl(ServicesCmp));

export default WrappedServicesCmp;
